import { fileSizeFormater } from "../components/file-item/utils";
import { LocalLabels } from "./localization";

/**
 * Spanish translation for Dropzone component
 */
export const DropzoneSpanish: LocalLabels = {
    defaultLabel: "Suelta tus archivos aqu",

    uploadingMessage: (amountOfFiles) => { return `Subiendo ${amountOfFiles} archivos`; },
    uploadFinished: (uploaded, rejected) => `Archivos subidos: ${uploaded}, Archivos rechazados: ${rejected}`,
    noFilesMessage: `No hay archivos vlidos pendientes por subir`,
    footer: {
        acceptAll: `Todos los tipos de archivo aceptados`,
        acceptCustom: (accept) => `File types: ${accept}`
    },
    header: {
        uploadFilesMessage: "Subir Archivos",
        maxSizeMessage: (maxFileSize) => `Tam. mximo de archivo ${maxFileSize} | `,
        validFilesMessage: (numberOfValidFiles, maxFiles) => ` Archivos ${numberOfValidFiles}/${maxFiles} | Vlidos: ${numberOfValidFiles} | `
    },
    fakeuploadsuccess: "El archivo se subi correctamente",
    fakeUploadError: "Error al subir el archivo",
}

/**
 * Spanish translation for FileItem component
 */
export const FileItemSpanish: LocalLabels = {
    fullInfoLayer: {
        name: `Nombre: `,
        size: "Tamao: ",
        type: "Tipo: "
    },
    status: {
        uploading: "Subiendo",
        success: "xito",
        valid: "vlido",
        denied: "no vlido",
        error: "error"

    },
}
/**
 * Spanish translation for Validation Errors
 */
 export const ValidateErrorSpanish: LocalLabels = {
    maxSizeError: (maxSize) => `El archivo es muy grande. El tam. mximo es ${fileSizeFormater(maxSize as number)}`,
    acceptError: `Tipo de archivo no permitido`,
    maxFileCount: (maxFiles) => `Cantidad mxima de archivos (${maxFiles}) alcanzada`
}